@media screen and (min-width: 770px) {
    /**************************
  ABOUT
  ***************************/
    .foto-profilo {
        float: left;
        margin: 80px 35px;
    }
    /**************************
        PORTFOLIO (%adattamento)
        5*3=15
        100-15=85
        85:3=28.33333
***************************/
    #gallery li {
        width: 28.33333%;
    }
    /**************************
        HEADER
***************************/
    #logo {
        float: left;
        width: 45%;
        margin-left: 5%;
        text-align: left;
    }
    nav {
        float: right;
        width: 45%;
        margin-right: 5%;
        background: none;
        text-align: right;
        padding-top: 15px;
        opacity: 1;
    }
    nav a {
        padding: 0 5px;
        font-weight: bold;
        font-size: 1.1em;
    }
    .main-header {
        box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.4);
    }
    .banner-tut {
        padding: 100px 20px 400px 20px;
    }
    #info {
        width: 45%;
        float: left;
    }
    #contatti {
        width: 50%;
        float: right;
    }
}

@media screen and (min-width: 950px) {
    /**************************
  ABOUT
  ***************************/
    .foto-profilo {
        float: left;
        margin: 80px 35px;
    }
    #delta {
        width: 320px;
        height: 60px;
        padding-bottom: 3px;
      }
    #gallery li {
        width: 20%;
    }
}
@media screen and (min-width: 1050px){
    #delta {
        width: 350px;
        height: 70px;
        padding-bottom: 3px;
      }
}